@media (max-width: 1024px) {
    .container{ height: 120px; }
    nav ul { display: block; }
    nav a { font-size: 10px; margin-top: 0px; }
    .header-box-more{ display: block; }
    #navblock{ display: none; }
    .nav { flex-wrap: wrap; }
    .btn { padding: 8px 12px; }
    .hero { text-align: center; }
    .hero button { align-self: center; }
    .social_media{ display: none; }
    .number_container{ display: none; }
    .nav nav{ display: none; }
    .logo{ align-self: center; }
    .logo_text{ text-align: center; }

    .hero-div1{ margin-left: 20px; }
    .hero-div2{max-width: 480px; max-height: none;}
    .producth2{ margin-left: 25px; }

    .about-texts{ width: 400px; margin-left: 90px;  }

    .burger{ display: block; }
}

@media (max-width: 820px) {
    .nav { flex-wrap: wrap; }
    .btn { padding: 8px 12px; }
    .hero { text-align: center; }
    .hero button { align-self: center; }
    .social_media{ display: none; }
    .number_container{ display: none; }
    .nav nav{ display: none; }
    .logo{ align-self: center; }
    .logo_text{ text-align: center; }

    .product{ max-width: 350px; }
    .product button{ margin-left: 130px; }

    .about-texts{ width: 300px; }

    
}

@media (max-width: 430px) {
    .hero-div1{ max-width: 350px; margin-left: 30px; }
    .hero-img{ border-radius: 0px; filter: brightness(50%);}

    .shop-grid{ display: flex; flex-direction: column; align-items: center;}
    .product{ align-items: center; }
    .product button{ margin-left: 230px; }

    .about-texts{ margin-top: 80px; margin-left: 50px; }
}