@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');
:root {
      --accent: #137f4b;
      --accent-light: #7fd19a;
      --text-muted: #6b7280;
      --bg: #f9fefb;
      --card: #ffffff;
      --shadow: 0 6px 20px rgba(19, 127, 75, 0.08);
      font-family: 'Inter', sans-serif;
      --height: auto;
    }

    * { box-sizing: border-box; margin: 0; padding: 0; }

    body { background: var(--bg); color: #111827; overflow: visible; }

    header { overflow: var(--height); position: sticky; top: 0; background: rgba(255, 255, 255, 0.8); backdrop-filter: blur(6px); z-index: 50; border-bottom: 1px solid rgba(0, 0, 0, 0.05); }

    .container { width: min(1100px, 92%); margin: auto; overflow-x: hidden; }

    .nav { display: flex; justify-content: space-between; align-items: center; padding: 20px 0; }

    #navblock{ margin-top: 30px; }

    .logo { display: flex; align-items: center; gap: 10px;}

    .logo svg { width: 40px; height: 40px; }

    .brand { font-weight: 700; font-size: 18px; color: var(--accent); }

    nav ul { display: flex; gap: 20px; list-style: none; }

    nav ul li a { text-decoration: none; color: var(--text-muted); font-weight: 600; font-size: 15px; transition: color 0.2s; }

    nav a:hover { color: var(--accent); }

    .number_container{
      align-items: end;
    }

    .social_media a{
      margin-left: 10px;
      color: #0e6c40;
    }

    .social_media{
      margin-left: 100px;
      font-size: 25px;
    }

    .contact_number1{
      font-size: 11px;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color: #0e6c40;
      text-align: end;
    }

    .contact_number2{
      font-size: 11px;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color: #0e6c40;
      text-align: end;
    }
    
    .btn { background: var(--accent); color: #fff; border: none; border-radius: 10px; padding: 10px 14px; text-decoration: none; font-weight: 600; cursor: pointer; transition: 0.2s; }

    .btn:hover { background: #0e6c40; }

    .hero { z-index: -1;  display: grid; grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); align-items: center; gap: 32px; padding: 60px 0; }

    .hero-div1{ max-width: 500px; margin-left: 100px;}

    .hero-div2{ max-width: 600px; }
    
    .hero h1 { font-size: clamp(28px, 4vw, 42px); line-height: 1.1; margin-bottom: 12px; }

    .hero p { color: var(--text-muted); margin-bottom: 18px; }

    .hero-img{ width: 100%; border-radius: 16px; object-fit: cover; max-height: 320px; }

    .search { display: flex; flex-direction: column; gap: 10px; background: var(--card); padding: 16px; border-radius: 12px; box-shadow: var(--shadow); }

    .search input { padding: 12px; border-radius: 10px; border: 1px solid #e6eef0; width: 100%; }

    .search button { align-self: flex-end; }

    .shop-grid { max-width: 1000px; margin: auto; display: grid; grid-template-columns: repeat(auto-fit, minmax(220px, 1fr)); gap: 20px; margin-top: 30px; } 
    
    .product { background: var(--card); border-radius: 14px; padding: 16px; box-shadow: var(--shadow); transition: transform 0.2s; }

    .product:hover { transform: translateY(-4px); }

    .product img { width: 100%; border-radius: 10px; height: 150px; object-fit: cover; }

    .product h4 { margin: 10px 0 6px; width: auto; }

    .price { font-weight: 700; color: var(--accent); width: auto;}

    .product button{ width: 80px; height: 25px; border: none; margin-left: 180px; background-color: #0e6c40; color: white; border-radius: 5px; font-size: 10px; font-family: 'Inter', sans-serif;}

    .product button a{ text-decoration: none; color: #e6eef0; }

    .about-texts{ max-width: 500px; height: auto; margin-left: 180px;}

    .about-texts h3{ color: #0e6c40; font-size: 30px; margin-top: -100px;}

    .about-texts p{margin-top: 20px; font-size: 20px;}

    .about { display: grid; grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); gap: 24px; padding: 60px 0; align-items: center; }

    .about p { color: var(--text-muted); }

    .card{ max-width: 500px; }
    
    .card h4{color: var(--accent);}

    form { margin-top: 20px; display: flex; flex-direction: column; gap: 10px; }

    form input, form textarea { padding: 10px; border-radius: 8px; border: 1px solid #e6eef0; }

    footer { text-align: center; padding: 24px 0; border-top: 1px solid rgba(0, 0, 0, 0.05); color: var(--text-muted); font-size: 14px; }

    .producth2{ margin-left: 100px; font-size: 28px; margin-bottom: 16px; color: var(--accent);}

    .p-header{ max-width: 1000px; height: 50px; margin: auto; border-bottom: 1px solid #0e6c40;}

    .p-header h1{ margin-top: 30px; color: #0e6c40; }

    .p-product{ max-width: 1000px; height: auto; margin: auto; margin-top: 50px; display: flex; flex-wrap: wrap; gap: 20px; justify-content: center;}

    .p-about{ max-width: 1000px; height: auto; margin: auto; }

    .about1{ margin-top: 50px; width: 100%; height: auto; border-bottom: 1px solid #0e6c40; }

    .about1 h1{ text-align: center; color: #0e6c40; }

    .about1 p{  margin: auto; margin-top: 20px; text-align: center; padding: 20px 10px;}

    .about2{ width: 100%; height: auto; border-bottom: 1px solid #0e6c40; }

    .about2 h1{ margin-top: 30px; color: #0e6c40; }

    .about2 p{ text-decoration: dotted; margin-left: 20px; padding: 20px 10px;}

    .about2-p1{ margin-top: 20px; }

    .about3{ width: 100%; height: auto; border-bottom: 1px solid #0e6c40; }

    .about3 h1{ margin-top: 20px; color: #0e6c40; }

    .about3 h6{ padding: 20px 0px; font-size: 18px; margin-top: 10px; font-style: normal; margin-left: 20px; color: #137f4b; }

    .about3-p{ margin-left: 20px; margin-top: 30px; font-size: 20px; color: #137f4b; }

    .about3 p{ margin-left: 80px; padding: 5px 10px; }

    .about4{ width: 100%; height: auto; }

    .about4 h1{ margin-top: 20px; margin-left: 10px; color: #0e6c40;}

    .about4-p{ margin-top: 20px; }

    .about4 p{ padding: 20px 10px; margin-left: 10px; }

    .header-box-more{ max-width: 1366px; margin: auto; display: none;}

    .header-more{ width: 50px; height: 50px; font-size: 20px; background-color: transparent; border: none; border-radius: 50%; color: #0e6c40; display: block; margin: auto;}

    .header-more:focus{ transition: all 0.5s; background-color: rgb(196, 196, 196); }

    .burger{ z-index: 99999999999; display: none; margin-left: 300px; position: absolute; }

    .offcanvas{ color: #0e6c40; z-index: 9999;}

    .offcanvas-body button{ margin-left: 300px; }

    .offcanvas-body ul{ margin-left: -20px; display: flex; flex-direction: row; justify-content: space-around; }

    .offcanvas-body ul li a{ border-bottom: 1px solid #0e6c40; margin-left: 0px; text-decoration: none; color: #0e6c40; }

    .offcanvas-body ul li{ list-style: none; margin-top: 10px; }

    .login-password-component{
      max-width: 330px;
      height: 480px;
      margin: 80px auto;
      border: 3px solid #0e6c40;
      border-radius: 20px;
      box-shadow: 0 0 10px gray;
    }

    .login-password-component h1{
      color: #0e6c40;
      font-size: 20px;
      text-align: center;
    }

    .login-password-component form{
      width: 300px;
      margin: 100px auto;
    }

    .login-password-component form button{
      height: 40px;
      background-color: #0e6c40;
      border: none;
      border-radius: 20px;
      margin-top: 20px;
      color: #fff;
      font-size: 15px;
      font-family: 'Inter', sans-serif;
    }